<template>
  <v-container
    fluid
    grid-list-md>
    <core-progress-modal :show="loading" />
    <v-form
      @submit.prevent="validateBeforeSubmit">
      <v-card class="elevation-0">
        <v-card-title>
          <span class="headline">Dados da Prático</span>
        </v-card-title>
        <v-card-text>
          <v-layout
            row
            wrap>
            <v-flex md9>
              <v-text-field
                v-validate="'required'"
                v-model="form.nome"
                :error-messages="errors.collect('Nome')"
                data-vv-name="Nome"
                label="Nome"
              />
            </v-flex>
            <v-flex md3>
              <v-text-field
                v-mask="'##.###.###/####-##'"
                v-validate="'required'"
                v-model="form.cnpj"
                :error-messages="errors.collect('CNPJ')"
                data-vv-name="CNPJ"
                label="CNPJ"
              />
            </v-flex>
            <v-flex md3>
              <v-text-field
                v-model="form.cnae"
                label="CNAE"
              />
            </v-flex>
            <v-flex md3>
              <v-text-field
                v-model="form.inscricaoMunicipal"
                label="Inscrição Municipal"
              />
            </v-flex>
            <v-flex md3>
              <v-text-field
                v-model="form.telefone"
                label="Telefones"
              />
            </v-flex>
            <v-flex md1>
              <v-text-field
                v-model="form.bin"
                label="BIN"
              />
            </v-flex>
            <v-flex md1>
              <v-text-field
                v-model="form.faixaInicialBin"
                label="Faixa Inicial"
                type="text"
              />
            </v-flex>
            <v-flex md1>
              <v-text-field
                v-model="form.faixaFinalBin"
                label="Faixa Final"
                type="text"
              />
            </v-flex>
            <v-flex md12>
              <core-endereco
                v-if="form.enderecoPratico"
                v-model="form.enderecoPratico"
              />
            </v-flex>
          </v-layout>
        </v-card-text>
      </v-card>
      <br>
      <v-card class="elevation-0">
        <v-card-title>
          <p>
            <span class="headline">Mensagem Boleto</span><br>
            Você pode personalizar a mensagem utilizando as hashtags: <br>
            <b>#MULTA#</b> será mostrado o percentual de multa a ser cobrado <br>
            <b>#JUROS#</b> será mostrado a taxa de juros cobrada por dia de atraso <br>
            <b>#CARENCIA#</b> dias de carência para cobrança de multa e juros <br>
            <b>#LOJA#</b> nome da loja para pagamento exclusivo <br>
            <b>#CENTRAL_ATENDIMENTO#</b> Telefones da central de atendimento.
          </p>
        </v-card-title>
        <v-card-text>
          <v-layout
            row
            wrap>
            <v-flex md12>
              <v-textarea
                v-model="form.instrucaoBoleto"
                label="Mensagem Boleto"
                rows="10"
                persistent-hint
                hint="Esse será a mensagem mostrada nas instruções de pagamento do boleto"
              />
            </v-flex>
          </v-layout>
        </v-card-text>
      </v-card>
      <br>
      <contas-pratico />
      <v-layout class="justify-end">
        <v-btn
          type="submit"
          color="primary"
        >Salvar</v-btn>
      </v-layout>
    </v-form>
  </v-container>
</template>

<script>
import Endereco from '../../beans/Endereco'
import { ConfiguracaoGeralBusiness } from '../../business'

const ContasPratico = () => import('./ContasPratico.vue')

export default {
  metaInfo: {
    titleTemplate: '%s - Configuração'
  },
  components: {
    ContasPratico
  },
  data () {
    return {
      loading: false,
      form: {
        nome: null,
        cnpj: null,
        telefone: null,
        inscricaoMunicipal: null,
        cnae: null,
        enderecoPratico: new Endereco(),
        bin: null,
        faixaInicialBin: null,
        faixaFinalBin: null,
        instrucaoBoleto: ''
      }
    }
  },
  beforeMount () {
    this.loading = true
    ConfiguracaoGeralBusiness.getConfiguracaoGeral()
      .then(response => {
        this.form = response.data
      }).catch(erro => {
        this.$root.showErro(erro.data)
      }).finally(() => {
        this.loading = false
      })
  },
  methods: {
    validateBeforeSubmit () {
      this.$validator.validate().then(result => {
        if (result) {
          this.loading = true
          ConfiguracaoGeralBusiness.salvar(this.form).then((response) => {
            this.$root.showSucesso('Dados enviados com sucesso')
          }).catch(() => this.$root.showErro('Erro ao salvar as configurações'))
            .finally(() => {
              this.loading = false
            })
        } else {
          this.$root.showAlerta(this.$vuetify.t('$vuetify.Erro.camposObrigatorios'))
        }
      })
    }
  }
}
</script>
